import React from 'react';
import Media from 'react-media';
import style from './event.module.css';
import Slider from './eventSlider/eventSlider';
import SliderDesk from './eventSlider/eventSliderDesk';

const Event = ({ onOpenModal }) => (
  <section className={style.section} id="Event" name="Event">
    <h2 className={style.header}>акции</h2>
    <div className={style.slider}>
      <Media query={{ maxWidth: 1199 }}>
        {matches => (matches ? <Slider /> : <SliderDesk />)}
      </Media>
    </div>
    <button onClick={onOpenModal} className={style.buttonOrder} type="button">
      оставить заявку
    </button>
  </section>
);

export default Event;
