import React from 'react';
import range from 'lodash/range';
import style from './carouselBottomControls.module.css';

const CarouselBottomControls = obj => {
  const { currentSlide, goToSlide, slideCount } = obj.obj;
  const arr = range(slideCount);
  return (
    <ul className={style.list}>
      {arr.map((el, i) => {
        if (currentSlide < i)
          return (
            <li className={style.listItem} key={el}>
              <button
                className={style.button}
                type="button"
                onClick={() => goToSlide(i)}
              >
                <span className={style.span} />
              </button>
            </li>
          );
        if (currentSlide === i)
          return (
            <li className={style.listItem} key={el}>
              <button className={style.button} type="button" onClick={null}>
                <span className={style.active} />
              </button>
            </li>
          );
        return (
          <li className={style.listItem} key={el}>
            <button
              className={style.button}
              type="button"
              onClick={() => goToSlide(i)}
            >
              <span className={style.span} />
            </button>
          </li>
        );
      })}
    </ul>
  );
};

export default CarouselBottomControls;
